<template>
	<view class="content">
		<view class="img_test">

			<view class="img_select">
				<view class="img_text" @click="seleckImage" v-if="imgHide">
					<span>选择图片</span>
				</view>
				<view class="img_src" v-else>
					<image mode="aspectFill" :src="imgSrc" @click="previewImage(imgSrc)" @longtap="deleteImage">
				</view>
			</view>

			<view class="img_answer">
				<image mode="aspectFill" :src="imgBase64.replace(/[\r\n]/g,'')" @click="previewImage(imgBase64)">
			</view>
		</view>


		<view class="control_menu">
			<button class="mini-btn" type="primary" @click="getAngle">开始测量</button>
			<button class="mini-btn" type="primary">保存结果</button>
		</view>

		<view class="test_answer">
			<text>平台本次测试的叶夹角为：</text><text class="num_answer">{{leafAngle}}°</text>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				imgSrc: '', // 选择的图片路径
				imgHide: true, // 控制图片显示组件显示与隐藏
				leafAngle: 0, // 叶夹角大小
				imgBase64: '', // 后端返回的base64
			}
		},
		onLoad() {

		},
		methods: {
			seleckImage() {
				/**
				 * @param {Number} count 最多可以选择的图片张数，默认9
				 * @param {Array} sizeType 可以指定是原图还是压缩图，默认二者都有
				 * @param {Array} sourceType album 从相册选图，camera 使用相机，默认二者都有
				 */
				uni.chooseImage({
					count: 1,
					sizeType: ['original', 'compressed'],
					sourceType: ['album', 'camera'],
					success: (res) => {
						this.imgSrc = res.tempFilePaths[0]
						this.imgHide = false
					}
				});
			},

			previewImage(img) {
				uni.previewImage({
					urls: [img],
				})
			},

			deleteImage() {
				uni.showModal({
					title: '删除图片',
					content: '确认删除图片？',
					success: (res) => {
						if (res.confirm) {
							this.imgSrc = ''
							this.imgHide = true
							this.leafAngle = 0
							this.imgBase64 = ''
						}
					}
				})
			},
			getAngle() {
				uni.getFileSystemManager().readFile({
					filePath: this.imgSrc,
					encoding: 'base64',
					success: (res) => {
						const base64 = 'data:image/jpeg;base64,' + res.data
						uni.request({
							url: 'http://192.168.1.105:3000/getAngle',
							method: "POST",
							data: {
								base64: base64,
								name: 'cufian'
							},
							success: (res) => {
								console.log(res);
								this.leafAngle = res.data.leafAngle
								this.imgBase64 = res.data.base64
								console.log(this.imgBase64);
							}
						})
					}
				})
			}
		}
	}
</script>

<style lang="less">
	.content {
		margin-top: 20rpx;
		padding: 40rpx;
		font-size: 35rpx;

		.img_test {
			display: flex;
			justify-content: space-around;
			border: 2rpx dashed green;
			height: 500rpx;
			line-height: 500rpx;

			.img_select {
				width: 50%;
				text-align: center;
				border-right: 1rpx dashed green;
			}

			.img_answer {
				width: 50%;
			}

			image {
				width: 94%;
				margin: 10rpx;
			}
		}


		.control_menu {
			display: flex;
			padding: 45rpx 10rpx 30rpx;
		}

		.test_answer {
			text-align: center;
			padding: 20rpx 0;
		}

		.num_answer {
			color: red;
		}

		.mini-btn {
			background-color: #5fb585;
		}
	}
</style>
